#page-view{
    width: 100%;
	height: auto;
    min-height: 100%;
	position: absolute;
	top: 0;
	left: 0;
    /* background: linear-gradient(180deg,rgb(166, 216, 199) , rgb(82, 178, 141)) */
    background: linear-gradient(0deg,rgb(82, 178, 141) , rgba(121, 196, 169, 0.3) 60%,rgb(166, 216, 199))
}
.button-view{
    width: 200px;
    height: 50px;
    border-radius: 8px;
    background: #289887;
}
.my-accordion .pad{
    border: 2px solid #99CBB9;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    background: #289887;
}
.zhun-button{
    border: 4px solid #99CBB9;
    background: #289887;
    border-radius: 28px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
     width: 50px;
     height: 50px;
}
.save-button{
    border: 2px solid #99CBB9;
    background: #289887;
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
     width: 110px;
     height: 50px;
      position: fixed;
       top: 50%;
        right: 0;
         z-index: 999
}
.my-accordion .am-accordion-item .am-accordion-header{
    background: #99CBB9;
}
.am-accordion .am-accordion-item .am-accordion-header {
    color: #289887;
    font-size: 18px;
    font-weight: 600;
    margin-left: 50px;
}
.am-accordion .am-accordion-item .am-accordion-header i{
    background-image: url('data:image/png;base64,');
}

.am-accordion-content-box {
    border-bottom: 1px solid #FFFFFF !important; 
}

.am-modal-transparent{
    width: 90%;
}

.am-modal-content{
    background-color: transparent !important;
}