#page-view{
    width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
    /* background: linear-gradient(180deg,rgb(166, 216, 199) , rgb(82, 178, 141)) */
    background: linear-gradient(0deg,rgb(82, 178, 141) , rgba(121, 196, 169, 0.3) 60%,rgb(166, 216, 199))
}
.button-view{
    width: 200px;
    height: 50px;
    border-radius: 8px;
    background: rgb(17,140,115);
}

input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: rgb(33,145,125);
    font-weight: 400;
}

input::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: rgb(33,145,125);
    font-weight: 400;
}

input:-ms-input-placeholder{
    color: rgb(33,145,125);
    font-weight: 400;
}

input::-webkit-input-placeholder {
    color: rgb(33,145,125);
    font-weight: 400;
}