#page-view{
    width: 100%;
	height: auto;
    min-height: 100%;
	position: absolute;
	top: 0;
	left: 0;
    /* background: linear-gradient(180deg,rgb(166, 216, 199) , rgb(82, 178, 141)) */
    background: linear-gradient(0deg,rgb(82, 178, 141) , rgba(121, 196, 169, 0.3) 60%,rgb(166, 216, 199))
}
.button-view{
    width: 200px;
    height: 50px;
    border-radius: 8px;
    background: #289887;
}
.mycollapsible{
    color: #289887; 
    font-size: 16px;
    font-weight:bold;
}
.collapsible-item{
    height: 50px;
    display: flex;
    flex-direction: row;
    border: 1px solid;
    border-radius: 4px;
    border-color: #289887;
    margin-top: 14px;
}

.collapsible-item-selected{
    height: 50px;
    display: flex;
    flex-direction: row;
    border: 1px solid;
    border-radius: 4px;
    background-color: #289887;
    border-color: #289887;
    margin-left: 24px;
    margin-right: 24px;
    margin-top: 14px;
}
